figure {
  margin: 0;
}

img,
picture {
  margin: 0;
  max-width: 100%;
  height: auto;
}

.embed-container { 
  position: relative; 
  padding-bottom: 56.25%; 
  height: 0; 
  overflow: hidden; 
  max-width: 100%; 
} 
.embed-container iframe,
.embed-container object,
.embed-container embed { 
  position: absolute; 
  top: 0; 
  left: 0; 
  width: 100%; 
  height: 100%; 
}

// Video player styles
.layout .plyr {
  font-family: $sans-serif;
  font-weight: 500;
  .plyr__control--overlaid,
  .plyr__control--overlaid:hover {
    background-color: $video-color;
  }
  .plyr__control.plyr__tab-focus, 
  .plyr__control:hover, 
  .plyr__control[aria-expanded=true] {
    background-color: $video-color;
  }
  input[type=range] {
    color: $video-color;
  }
}